import { Nav } from "./styles.js";
import React from "react";
import { Link } from "react-router-dom";

export default function Navbar({}) {

    return (
        <Nav>
            <Link to="/">Inicio</Link>
            <Link to="/signup">Registrarse</Link>
            <Link to="/login">Iniciar sesión</Link>
        </Nav>
    )
}