import React from "react";
import { useSelector } from "react-redux";

export default function Profile({}) {
    const user = useSelector(state=> state.users.data);
    console.log(user?.get("fileId"))
    return (
        <div>
            <img src={user?.get("fileUrl")}/>
            {user?.get("name")}
        </div>
    );
}